import { WerkDataSet } from './../nachlass/nachlass.component';
import { AuthorBibEntry } from 'src/app/pages/bibliothek/bibliothek.component';
import { Component, OnInit, Input } from '@angular/core';
import { faQuoteRight, faLink } from '@fortawesome/free-solid-svg-icons';

@Component({
  selector: 'app-card-footer',
  templateUrl: './card-footer.component.html',
  styleUrls: ['./card-footer.component.scss']
})
export class CardFooterComponent implements OnInit {

  @Input('BibEntry') bibEntry?: AuthorBibEntry
  @Input('NachlassEntry') nachlassEntry?: WerkDataSet
  @Input('pid') pid?: string

  entry: AuthorBibEntry | WerkDataSet

  qouteRight = faQuoteRight
  linkIcon = faLink

  constructor() { }

  ngOnInit(): void {
    if (this.bibEntry != undefined) {
      this.entry = this.bibEntry
    }
    if (this.nachlassEntry != undefined) {
      this.entry = this.nachlassEntry
    }
  }

  isProperty(obj: any, key: string): boolean {
    if (obj == undefined) {
      return false
    } else {
      return obj.hasOwnProperty(key)
    }
  }

}
