import { TranslateService } from '@ngx-translate/core';
import { AuthorBibEntry } from 'src/app/pages/bibliothek/bibliothek.component';
import { Component, Input, OnInit } from '@angular/core';
import { environment } from 'src/environments/environment';
import { faInfoCircle } from '@fortawesome/free-solid-svg-icons';

@Component({
  selector: 'app-bib-full-entry',
  templateUrl: './bib-full-entry.component.html',
  styleUrls: ['./bib-full-entry.component.scss']
})
export class BibFullEntryComponent implements OnInit {

  @Input('entry') entry: AuthorBibEntry
  @Input('pid') pid: string

  selectedLanguage = environment.defaultLanguage
  infoIcon = faInfoCircle

  constructor(private translate: TranslateService) { }

  ngOnInit(): void {
    this.translate.onLangChange.subscribe((language) => {
      this.selectedLanguage = language.lang
    })
  }

  isProperty(obj: any, key: string): boolean {
    if (obj == undefined) {
      return false
    } else {
      return obj.hasOwnProperty(key)
    }
  }

}
