import { Pipe, PipeTransform } from '@angular/core';
import { Author } from '../components/nachlass/nachlass.component';

@Pipe({
  name: 'printAuthor'
})
export class PrintAuthorPipe implements PipeTransform {

  transform(value: Author | undefined): string {

    if (value == undefined) {
      return ''
    }

    let returnValue = '';

    if (this.isProperty(value, 'surname')) {
      returnValue += value.surname
      if (this.isProperty(value, 'forename')) {
        returnValue += ', '
        returnValue += value.forename
      }
    } else if (this.isProperty(value, 'name')) {
      returnValue += value.name;
    } else {
      returnValue += 'Author Object empty'
    }
    return returnValue;
  }

  isProperty(obj: any, key: string): boolean {
    if (obj == undefined) {
      return false
    } else {
      return obj.hasOwnProperty(key)
    }
  }
}
