import { environment } from './../../../environments/environment';
import { TranslateService } from '@ngx-translate/core';
import { WerkDataSet } from './../nachlass/nachlass.component';
import { Component, Input, OnInit } from '@angular/core';
import { faInfoCircle, faEnvelope } from '@fortawesome/free-solid-svg-icons';

@Component({
  selector: 'app-catalog-entry',
  templateUrl: './catalog-entry.component.html',
  styleUrls: ['./catalog-entry.component.scss']
})
export class CatalogEntryComponent implements OnInit {

  infoIcon = faInfoCircle
  envelopeIcon = faEnvelope;

  selectedLanguage = environment.defaultLanguage

  @Input('dataSet') dataSet: WerkDataSet;
  @Input('type') type?: string;


  constructor(private translate: TranslateService) { }

  ngOnInit(): void {
    this.translate.onLangChange.subscribe((language) => {
      this.selectedLanguage = language.lang
    })
  }

  isProperty(obj: any, key: string): boolean {
    if (obj == undefined) {
      return false
    } else {
      return obj.hasOwnProperty(key)
    }
  }

}
