import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'stringcap'
})
export class StringcapPipe implements PipeTransform {

  transform(value: string | undefined, maxLength: number, replace: string): string {
    if (value == undefined) {
      return ''
    }
    if (value.length > maxLength) {
      value = value.substring(0, maxLength)
      value += replace
    }
    return value;
  }

}
