////////BibData//////////
//contains all bib data authors and entrys
export interface BibData {
  authors: AuthorEntry[]
  noAutor: AuthorBibEntry[]
}

/////////AuthorEntry////////
//contains the name of an Author and all of his bib entrys
export interface AuthorEntry {
  isFirstLetter?: string //When this entry is the first of this alphabet than a property with the first letter will be set. Enables navigation from the sticky Navbar
  name: Author
  entrys: AuthorBibEntry[]
}

//////////AuthorBibEntry////////
//contains one Entry from an Author
export interface AuthorBibEntry {
  id: string ///z.B. SZDBIB.2
  title: TitleTypes
  author: Authors
  date?: string
  edition: TextTranslations | string
  publication: PublicationData
  seriesStmt: SeriesStmt
  textLang: languageCodes[]
  physDesc: physDesc
  note?: TextTranslations
  provenance: Provenance[]
  altIdentifier: AltIdentifier[]
  historyProvenance: TextTranslations
  msIdentifier: MSIdentifier
}

///////////AltIdentifier///////////
//a tei Structure
//local because it is diffrently implemented in Nachlass
interface AltIdentifier {
  idno: string
  corresp: CorrespType
  n: number
  type: AltIdentifierType
  note?: TextTranslations
}

////////AltIdentifierType/////////
type AltIdentifierType = "capital" | "lowercase"


//////////CorrespType///////////
type CorrespType = "szdg:Hausexemplar" | "szdg:InventoryNumberOld" | "szdg:SingleLetter"

////////////provenance///////////
//a group from the tei
export interface Provenance {
  ref: string
  term: TextTranslations
  desc: TextTranslations
  stamp: boolean
}

////////////physDesc/////////////
//a group from the tei
export interface physDesc {
  pageMeasure?: string | TextTranslations
  leafMeasure?: number
  formatMeasure?: string | TextTranslations
  extent?: TextTranslations[]
  binding?: TextTranslations
}

/////////////PublicationData///////////
//Contains all inforamtion about the publisher
export interface PublicationData {
  publisher: string
  date: string
  pubPlace: string
}

//////////TitleTypes///////
//defines the possible title types
export interface TitleTypes {
  title?: string
  original?: string
  series?: string
}

///////SeriesStmt////////
//a group from the tei
export interface SeriesStmt {
  title: string
  unterreihe?: string
}

////////Authors////////////
//contains all possible author roles
export interface Authors {
  author?: Author
  composer?: Author
  editor?: Author
  illustrator?: Author
  translator?: Author
  preface?: Author
  afterword?: Author
}

