////////////pidType////////////////
//defines all possible page ids to avoid faulty ones
export type pidType = 'o:szd.werke' | 'o:szd.lebensdokumente' | 'o:szd.nli' | 'o:szd.marbach' | 'o:szd.bibliothek'  //This type should be defined somewhere else because it is used more global

//////////// HeaderCategorySubList////////////
//Can be used as a HeaderCategory instead of an string
//Used to enable sublist in the header
//name: is the title of the list
//items: are the Elements of the sublist
//
//Still has to be implemented in the stickynavbar component
export interface HeaderCategorySubList {
  name: string
  items: string[]
}

//////// WerkeDataCategory/////////
//Defines an category (z.B. Werke -> Autobiographisches) and contains all of its subcategorys
export interface WerkeDataCategory {
  name: string
  subCategorys: WerkeDataSubCategory[]
}

//////////// WerkeDataSubCategory/////////
//Defines an SubCategory (z.B. Werke -> Autobiographischer Aufsatz) and contains all of its datasets
export interface WerkeDataSubCategory {
  name: string
  dataSets: WerkDataSet[]
}

///////////WerkDataSet////////
//The structure of one DataSet (z.B. Werke ->  Autobiographischer Aufsatz | Typoskriptdurchschlag | SZ-AAP/W30)
//optional flags still have to be set (adding ? before :). All Parameters are evaluated if they exist in html
export interface WerkDataSet {
  acquisition: TextTranslations
  additionalMaterial: TextTranslations
  author: Author
  binding: TextTranslations
  classification: TextTranslations
  category: string
  dateOfPublication: string
  editor: Author
  enclosures?: (EnclosureData)[]
  extent: ExtentData
  foliation: TextTranslations
  id: string //SZDMSK....
  identifyingInscription: TextTranslations
  incipit: string
  keywords: Keywords
  location: string
  msIdentifier: MSIdentifier
  originalDate: string
  originalPlace: string
  originaltitle: string
  note: TextTranslations
  objecttyp: TextTranslations
  provenance: TextTranslations
  relationToPerson: string
  secretarialHand: TextTranslations
  signature: string
  summary?: TextTranslations
  term: string
  textLang: languageCodes[]
  title: TitleData
  writingInstrument: TextTranslations
  writingMaterial: TextTranslations
}


/////////TextTranslations/////////
//A replacemnt for strings with two languages, value will be selected depending on the current language with the printEnDe pipe
export interface TextTranslations {
  de: string
  en: string
}


//////////////AltIdentifier/////
//A object from the Werke TEI - needed for the Title of an dataSet
//needs to be local since a diffrent one is defined in bib
interface AltIdentifier {
  subject?: string
  PID?: string
  extern?: string
}

////////////Author////////
//Defines an Author with forename and surname
export interface Author {
  surname?: string
  forename?: string
  name?: string
}

////////////Keywords////////
//A object Structure from the Werke TEI
export interface Keywords {
  szdgEnclosures?: Reference
  classification?: TextTranslations
  reference?: Reference[]
}

//////////Reference////////
//Defines an Reference, is mostly used to define links to other dataSet entrys
export interface Reference {
  type?: string
  information: string
  ref: string
}

/////////TitleData/////////
//Defines the tiles of an dataSet
export interface TitleData {
  Einheitsachtitel?: TextTranslations | string
  Gesamttitel?: string
  ana: anaTitle
}

//////////anaTitle//////////
//Defines the type of an ana Title
export interface anaTitle {
  type: anaType
  title: TextTranslations | string
}

//////////anaType/////////////
//type of ana Title: This type is shown with the title and used for the translation key
export type anaType = 'assigned' | 'supplied/verified' | 'original' | 'none'

/////////EnclosureData/////////
// Its either description and measure or ref
export interface EnclosureData {
  description?: TextTranslations,
  measure?: string,
  ref?: EnclosureRefData
}

/////////EnclosureRefData///////
//Reference for an Enclosure, it replaces the data in an EnclosureData object
export interface EnclosureRefData {
  text: string,
  target: string
}

////////ExtentData///////////
///A object Structure from the Werke TEI,
export interface ExtentData {
  objecttyp: TextTranslations
  measureCorrected: TextTranslations
  measure?: string
}

/////////////MSIdentifier/////////
//A object Structure from the Werke TEI
export interface MSIdentifier {
  country?: string
  settlement: string
  repository: Reference
  idno?: IDNO[]
  id?: string  //used in the bib Identifier
  altIdentifier?: AltIdentifier
}


/////////IDNO///////////
/// References an ID
export interface IDNO {
  type?: string  //the type isnt used in bib
  value: string
}

///////languageCodes////////
//all Language Codes, are used to select the correct Language translation
export type languageCodes = 'ger' | 'eng' | 'fre' | 'ita' | 'spa' | 'yid'

