import { Component, Input, OnInit } from '@angular/core';
import { AuthorBibEntry } from 'src/app/pages/bibliothek/bibliothek.component';

@Component({
  selector: 'app-bib-entry-header',
  templateUrl: './bib-entry-header.component.html',
  styleUrls: ['./bib-entry-header.component.scss']
})
export class BibEntryHeaderComponent implements OnInit {

  @Input('toggleId') toggleId: string = ''
  @Input('entry') entry: AuthorBibEntry
  @Input('pid') pid: string = ''

  constructor() { }

  ngOnInit(): void {
  }

  isProperty(obj: any, key: string): boolean {
    if (obj == undefined) {
      return false
    } else {
      return obj.hasOwnProperty(key)
    }
  }

}
