import { TranslateService } from '@ngx-translate/core';
import { Pipe, PipeTransform } from '@angular/core';
import { TextTranslations } from '../components/nachlass/nachlass.component';

@Pipe({
  name: 'printEnDe'
})
export class PrintEnDePipe implements PipeTransform {

  constructor() {
  }

  transform(value: string | TextTranslations | undefined, language: string): string {
    if (value == undefined) {
      return ''
    }
    if (typeof value == 'string') {
      return value
    } else {
      if (language == 'en') {
        return value.en
      } else {
        return value.de
      }
    }
  }


}
